/** @jsxImportSource @emotion/react */
import React from 'react'
import { Point } from '../../types'
import { Layer, Source } from 'react-map-gl'

export interface MatchedSiteLineProps {
  from: Point
  to: Point
}

export const MatchedSiteLine = ({ from, to }: MatchedSiteLineProps): JSX.Element => {
  return (
    <Source
      id="MatchedSiteLine"
      type="geojson"
      data={{
        type: 'Feature',
        properties: {},
        geometry: {
          type: 'LineString',
          coordinates: [
            [from.longitude, from.latitude],
            [to.longitude, to.latitude],
          ],
        },
      }}
    >
      <Layer
        // id="lineLayer"
        type="line"
        source="MatchedSiteLine" // TODO: fix bug where going back to sample with line throws console error
        layout={{
          'line-join': 'round',
          'line-cap': 'round',
        }}
        paint={{
          'line-color': '#aa00aa', // TODO
          'line-width': 2,
          'line-dasharray': [2, 2],
        }}
      />
    </Source>
  )
}
