/** @jsxImportSource @emotion/react */
import React, { useEffect } from 'react'
import { useRecoilValue } from 'recoil'
import MuiIconButton from '@mui/material/IconButton'
import CloseIcon from '@mui/icons-material/Close'
import MuiSkeleton from '@mui/material/Skeleton'

import { ReadOnlyField } from '../ReadOnlyField'
import { Site } from '../../types'
import { lookupListToOptions, selectEcosystems, selectSystems, selectWaterbodyTypes } from '../../data/selectors'
import { withSuspense } from '../withSuspense'
import { NumberField } from '../NumberField'
import { PinSvg } from '../SitesListing/PinMarker'
import { TextField } from '../TextField'
import { ReadOnlyUsStateField } from './ReadOnlyUsStateField'
import { AutocompleteField } from '../AutocompleteField'
import { useUpdatableState } from '../../lib/useUpdatableState'

export interface SiteDetailPanelProps {
  sampleCount: number
  value: Site
  onChange: (newValue: Site) => void
  onClose?: () => void
  children?: React.ReactNode
}

interface Fields extends Omit<Site, 'latitude' | 'longitude' | 'usStateId'> {
  latitude: number | null
  longitude: number | null
}

export const NewSiteDetailPanel = withSuspense(
  ({ sampleCount, value, onChange, onClose, children }: SiteDetailPanelProps): JSX.Element => {
    const systemOptions = lookupListToOptions(useRecoilValue(selectSystems))
    const ecosystemOptions = lookupListToOptions(useRecoilValue(selectEcosystems))
    const waterbodyTypeOptions = lookupListToOptions(useRecoilValue(selectWaterbodyTypes))

    const [state, setState, updateState] = useUpdatableState<Fields>(value)

    const { name, latitude, longitude, systemId, ecosystemId, waterbodyName, waterbodyTypeId } = state

    const errors: Partial<Record<keyof Fields, boolean>> | undefined = (() => {
      const result = {
        name: !state.name.trim(),
        latitude: !latitude,
        longitude: !longitude,
      }
      if (!Object.values(result).some((value) => value)) return undefined
      return result
    })()

    const handleSave = () => {
      if (errors) return
      onChange({
        id: value.id,
        usStateId: 0, // TODO: make optional in type?
        name: state.name.trim(),
        latitude: latitude ?? 0,
        longitude: longitude ?? 0,
        systemId,
        ecosystemId,
        waterbodyName: state.name.trim(),
        waterbodyTypeId,
      })
    }

    useEffect(() => {
      handleSave()
    }, [state])

    return (
      <div css={{ padding: '0.5em' }}>
        <div css={{ display: 'flex', alignItems: 'center', paddingLeft: '0.8em' }}>
          <div
            css={{
              width: 20,
              marginRight: '0.5em',
            }}
          >
            <PinSvg />
          </div>
          <TextField
            InputProps={{ sx: { fontWeight: 'bold', fontSize: '120%' } }}
            value={name}
            onChange={(value) =>
              updateState((draft) => {
                draft.name = value
              })
            }
            onBlur={handleSave}
            error={errors?.name}
          />
          {onClose && (
            <div css={{ marginLeft: 'auto' }}>
              <MuiIconButton onClick={onClose}>
                <CloseIcon />
              </MuiIconButton>
            </div>
          )}
        </div>
        <div
          css={{
            marginTop: '1em',
            display: 'grid',
            gridTemplateColumns: '1fr 1fr',
            gridTemplateRows: `
              auto
              auto
              1em
              auto
              auto
              1em
              auto
                `,
            gap: '0.5em',
            gridTemplateAreas: `
              "usState id"
              "latitude longitude"
              ". ."
              "system ecosystem"
              "waterbodyName waterbodyType"
              ". ."
              "sampleCount ."
            `,
          }}
        >
          <ReadOnlyUsStateField gridArea="usState" label="US State" point={value} />
          <ReadOnlyField gridArea="id" label="ID" value="TBD" />
          <NumberField
            gridArea="latitude"
            label="Latitude"
            value={latitude}
            onChange={(value) =>
              updateState((draft) => {
                draft.latitude = value
              })
            }
            step={0.0001}
            error={errors?.latitude}
          />
          <NumberField
            gridArea="longitude"
            label="Longitude"
            value={longitude}
            onChange={(value) =>
              updateState((draft) => {
                draft.longitude = value
              })
            }
            step={0.0001}
            error={errors?.longitude}
          />
          <AutocompleteField<number>
            gridArea="system"
            label="System"
            value={systemId ?? null}
            onChange={(value) =>
              updateState((draft) => {
                draft.systemId = value
              })
            }
            options={systemOptions}
            isClearable
          />
          <AutocompleteField<number>
            gridArea="ecosystem"
            label="Ecosystem"
            value={ecosystemId ?? null}
            onChange={(value) =>
              updateState((draft) => {
                draft.ecosystemId = value
              })
            }
            options={ecosystemOptions}
            isClearable
          />
          <TextField
            gridArea="waterbodyName"
            label="Waterbody"
            value={waterbodyName || ''}
            onChange={(value) =>
              updateState((draft) => {
                draft.waterbodyName = value
              })
            }
          />
          <AutocompleteField<number>
            gridArea="waterbodyType"
            label="Waterbody Type"
            value={waterbodyTypeId ?? null}
            onChange={(value) =>
              updateState((draft) => {
                draft.waterbodyTypeId = value
              })
            }
            options={waterbodyTypeOptions}
            isClearable
          />
          <ReadOnlyField gridArea="sampleCount" label="# Samples" value={sampleCount} />
        </div>
        {children}
      </div>
    )
  },
  () => null
)
