import React, { useEffect, useState } from 'react'
import { useRecoilValue } from 'recoil'

import {
  allSitesStatusAtom,
  FETCH_ALL_SITES_STATUS,
  selectLatestSubmissionByBoxId,
  useFetchAllSitesOptimized,
} from '../../data/selectors'
import { PendingBoxReview as Component } from './PendingBoxReview'
// import { Fallback } from './Fallback'
import { withSuspense } from '../withSuspense'

export interface PendingBoxReviewProps {
  boxId: string | number
}

export const PendingBoxReview = withSuspense(
  ({ boxId }: PendingBoxReviewProps): JSX.Element => {
    const submission = useRecoilValue(selectLatestSubmissionByBoxId(Number(boxId)))

    const allSitesStatus = useRecoilValue(allSitesStatusAtom)
    const fetchAllSitesOptimized = useFetchAllSitesOptimized()

    const [isReady, setIsReady] = useState(false)

    useEffect(() => {
      if (allSitesStatus === FETCH_ALL_SITES_STATUS.unfetched) {
        fetchAllSitesOptimized()
      } else if (FETCH_ALL_SITES_STATUS.fetched) {
        setIsReady(true)
      }
    }, [allSitesStatus])

    if (!submission) return <h2>Box {boxId} not found.</h2>

    if (!isReady) return <div>loading... TODO</div> // TODO: pass this as prop instead?

    return <Component submission={submission} />
  },
  () => <div>loading... TODO</div>
)
